import { LineProfile } from "./line-profile";
import { LoginProvider } from "./login-provider";

export abstract class BaseLoginProvider implements LoginProvider {
    constructor() { }
    abstract initialize(): Promise<void|any>;
    abstract getLoginStatus(loginStatusOption?: any): Promise<LineProfile>;
    abstract signIn(signInOption?: any): Promise<LineProfile>;
    abstract signOut(revoke?: boolean): Promise<void>;
    abstract providerCore(): Promise<any>;

    protected loadScript(
        id: string,
        src: string,
        onload: any,
        parentElement?: any
    ): void {
        // get document if platform is only browser
        if(typeof document !== 'undefined' && !document.getElementById(id)) {
            const signInJs: HTMLScriptElement = document.createElement('script');  
            signInJs.async = true;
            signInJs.src = src;
            signInJs.onload = onload;
            
            if (!parentElement) parentElement = document.head;
            parentElement.appendChild(signInJs);
        }
    }
}