import { Inject, Injectable } from '@angular/core';
import { AsyncSubject, Observable, ReplaySubject } from 'rxjs';
import { LineProfile } from './entities/line-profile';
import { LoginProvider } from './entities/login-provider';

/**
 * An interface to define the shape of the service configuration options.
 */
export interface LineLiffServiceConfig {
    autoLogin?: boolean;
    providers: { id: string; provider: LoginProvider }[];
    onError?: (error: any) => any;
}

@Injectable()
export class NgxLineLiffService {
    private static readonly ERR_LOGIN_PROVIDER_NOT_FOUND = 'Login provider not found';
    private static readonly ERR_NOT_LOGGED_IN = 'Not logged in';
    private static readonly ERR_NOT_INITIALIZED = 'Login providers not ready yet. Are there errors on your console?';
    private static readonly ERR_NOT_SUPPORTED_FOR_REFRESH_TOKEN = 'Chosen login provider is not supported for refreshing a token';

    private providers: Map<string, LoginProvider> = new Map();
    private autoLogin = false;

    private _user: LineProfile | null = null;
    private _authState: ReplaySubject<LineProfile | null> = new ReplaySubject(1);

    /* Consider making this an enum comprising LOADING, LOADED, FAILED etc. */
    private initialized = false;
    private _initState: AsyncSubject<boolean> = new AsyncSubject();
    /** An `Observable` that one can subscribe to get the current logged in user information */
    public get authState(): Observable<LineProfile | null> {
        return this._authState.asObservable();
    }
    /** An `Observable` to communicate the readiness of the service and associated login providers */
    public get initState(): Observable<boolean> {
        return this._initState.asObservable();
    }

    constructor(
        @Inject('LineLiffServiceConfig') config: LineLiffServiceConfig | Promise<LineLiffServiceConfig>
    ) {
        if (config instanceof Promise) {
            config.then(config => this.initialize(config));
        } else {
            this.initialize(config);
        }
    }

    private initialize(config: LineLiffServiceConfig) {
        this.autoLogin = (config.autoLogin !== undefined) ? config.autoLogin : false;
        const { onError = console.error } = config;

        config.providers.forEach(item => this.providers.set(item.id, item.provider));

        Promise.all(
            Array.from(this.providers.values()).map(provider => provider.initialize())
        ).then((provide: any) => {
            if (this.autoLogin) {
                const loginStatusPromises: Array<any> = new Array();
                let loggedIn = false;

                this.providers.forEach((provider: LoginProvider, key: string) => {
                    let promise = provider.getLoginStatus();
                    loginStatusPromises.push(promise);
                    promise.then((user: LineProfile) => {
                        this._user = user;
                        this._authState.next(user);
                        loggedIn = true;
                    }).catch(console.debug);
                });
                Promise.all(loginStatusPromises).catch(() => {
                    if (!loggedIn) {
                        this._user = null;
                        this._authState.next(null);
                    }
                });
            }
        }).catch(err => {
            onError(err);
        }).finally(() => {
            this.initialized = true;
            this._initState.next(this.initialized);
            this._initState.complete();
        });
    }
    public getLoginStatus(): Promise<LineProfile> {
        return new Promise((resolve, reject) => {
            if (!this.initialized) {
                reject(NgxLineLiffService.ERR_NOT_INITIALIZED);
            } else {
                const providerObj = this.providers.get('LINE');
                if (providerObj) {
                    providerObj.getLoginStatus().then(user => {
                        this._user = user;
                        this._authState.next(user);
                        resolve(user);
                    }, err => reject(err)).catch(err => reject(err));
                } else {
                    reject(NgxLineLiffService.ERR_LOGIN_PROVIDER_NOT_FOUND);
                }
            }
        });
    }
    /**
     * A method used to sign in a user with a specific `LoginProvider`.
     * @param signInOption Optional `LoginProvider` specific arguments
     * @returns A `Promise` that resolves to the authenticated user information
     */
    public signIn(signInOption?: { redirectUri?: string }): Promise<LineProfile> {
        return new Promise((resolve, reject) => {
            if (!this.initialized) {
                reject(NgxLineLiffService.ERR_NOT_INITIALIZED);
            } else {
                let providerObj = this.providers.get('LINE');
                if (providerObj) {
                    providerObj.signIn(signInOption).then((user: LineProfile) => {
                        this._user = user;
                        this._authState.next(user);
                        resolve(user);
                    }).catch(err => reject(err));
                } else {
                    reject(NgxLineLiffService.ERR_LOGIN_PROVIDER_NOT_FOUND);
                }
            }
        });
    }
    /**
     * A method used to sign out the currently loggen in user.
     * @returns A `Promise` that resolves if the operation is successful, rejects otherwise
     */
    public signOut(): Promise<void> {
        return new Promise((resolve, reject) => {
            if (!this.initialized) {
                reject(NgxLineLiffService.ERR_NOT_INITIALIZED);
            } else if (!this._user) {
                reject(NgxLineLiffService.ERR_NOT_LOGGED_IN);
            } else {
                const providerObj = this.providers.get('LINE');
                if (providerObj) {
                    providerObj.signOut().then(() => {
                        resolve();
                        this._user = null;
                        this._authState.next(null);
                    }).catch(err => reject(err));
                } else {
                    reject(NgxLineLiffService.ERR_LOGIN_PROVIDER_NOT_FOUND);
                }
            }
        });
    }

    /**
     * A method used to get provider core.
     * @returns A `Promise` that resolves to the provider core
     */
    public providerCore(): Promise<any> {
        return new Promise((resolve, reject) => {
            if (!this.initialized) {
                reject(NgxLineLiffService.ERR_NOT_INITIALIZED);
            } else if (!this._user) {
                reject(NgxLineLiffService.ERR_NOT_LOGGED_IN);
            } else {
                const providerObj = this.providers.get('LINE');
                if (providerObj) {
                    providerObj.providerCore().then(providerCore => resolve(providerCore)).catch(err => reject(err));
                } else {
                    reject(NgxLineLiffService.ERR_LOGIN_PROVIDER_NOT_FOUND);
                }
            }
        });
    }
}