import liff from '@line/liff';
import { BaseLoginProvider } from '../entities/base-login-provider';
import { LineProfile } from '../entities/line-profile';

// declare let liff: any; //-- Use for loadScript js cdn
type UnPromise<T> = T extends Promise<infer X> ? X : T;
interface InitOptions {
    liffId?: string;
}
export class LineLiffLoginProvider extends BaseLoginProvider {
    public static readonly PROVIDER_ID: string = 'LINE';
    public static readonly ERR_LIFF_ID_NOT_FOUND: string = 'LIFF ID is not found.';

    private OS?: ReturnType<typeof liff.getOS>;
    private profile?: UnPromise<ReturnType<typeof liff.getProfile>>;
    protected authLiff: any;

    constructor(
        private channelId: string,
        private initIptions: InitOptions = {}
    ) { super(); }

    public initialize(): Promise<void | any> {
        return new Promise((resolve, reject) => {
            try {
                if (!this.initIptions?.liffId) {
                    reject(LineLiffLoginProvider.ERR_LIFF_ID_NOT_FOUND);
                } else {
                    // this.loadScript(
                    //     LineLiffLoginProvider.PROVIDER_ID,
                    //     'https://static.line-scdn.net/liff/edge/versions/2.14.0/sdk.js',
                    //     () => {
                    //         liff.init({ liffId: String(this.initIptions.liffId) }).then(() => {
                    //             this.OS = liff.getOS();
                    //             if (liff.isLoggedIn()) liff.getProfile().then((profile: LineProfile) => this.profile = profile, (err: any) => reject(err)).catch(console.error);
                    //             resolve();
                    //         }).catch(console.error);
                    //     }
                    // );
                    liff.init({ liffId: this.initIptions.liffId }).then(() => {
                        this.OS = liff.getOS();
                        if (liff.isLoggedIn()) liff.getProfile().then(profile => this.profile = profile).catch(console.error);
                        resolve(liff);
                    }).catch(console.error);
                }
            } catch (err) {
                reject(err);
            }
        });
    }

    public getLoginStatus(loginStatusOption?: any): Promise<LineProfile> {
        return new Promise((resolve, reject) => {
            if (liff.isLoggedIn()) {
                liff.init({ liffId: String(this.initIptions.liffId) }).then(() => {
                    liff.getProfile().then((profile: LineProfile) => {
                        this.profile = profile;
                        resolve(profile);
                    }, (err: any) => reject(err)).catch(console.error);
                }).catch(console.error);
            } else {
                reject(null);
            }
        });
    }

    public signIn(signInOption?: any): Promise<LineProfile> {
        return new Promise((resolve, reject) => {
            try {
                if (liff.isLoggedIn()) {
                    liff.getProfile().then((profile: LineProfile) => {
                        this.profile = profile;
                        resolve(profile);
                    }, (err: any) => reject(err)).catch(console.error);
                } else if (signInOption?.redirectUri) {
                    liff.init({ liffId: String(this.initIptions.liffId) }).then(() => {
                        liff.login({ redirectUri: signInOption.redirectUri });
                    }).catch(console.error);
                } else {
                    liff.init({ liffId: String(this.initIptions.liffId) }).then(() => {
                        liff.login();
                    }).catch(console.error);
                }
            } catch (error) {
                reject(error);
            }
        });
    }

    public signOut(revoke?: boolean): Promise<void> {
        return new Promise((resolve, reject) => {
            try {
                if (liff.isLoggedIn()) {
                    liff.init({ liffId: String(this.initIptions.liffId) }).then(() => {
                        resolve();
                        liff.logout();
                    }).catch(console.error);
                } else {
                    resolve();
                }
            } catch (error) {
                reject(error);
            }
        });
    }

    /** Running in LIFF browser only. */
    public providerCore(): Promise<any> {
        return new Promise((resolve, reject) => {
            try {
                if (liff.isInClient()) {
                    resolve(liff);
                } else {
                    reject('Running in LIFF browser only.');
                }
            } catch (error) {
                reject(error);
            }
        });
    }
}