import { CommonModule } from '@angular/common';
import { ModuleWithProviders, NgModule, Optional, SkipSelf } from '@angular/core';
import { NgxLineLiffService } from './ngx-line-liff.service';

@NgModule({
    imports: [
        CommonModule,
    ],
    providers: [
        NgxLineLiffService
    ]
})
export class NgxLineLiffModule {
    constructor(@Optional() @SkipSelf() parentModule: NgxLineLiffModule) {
        if (parentModule) throw new Error('NgxLineLiffModule is already loaded. Import it in the AppModule only');
    }

    public static initialize(config: any): ModuleWithProviders<NgxLineLiffModule> {
        return {
            ngModule: NgxLineLiffModule,
            providers: [
                NgxLineLiffService,
                { provide: 'LineLiffServiceConfig', useValue: config }
            ]
        }
    }
}
